  /* Typography */
  --font-menu: "Space Mono", monospace;
  --font-paragraph: "Space Mono", monospace;
  --font-title: "Rubik", sans-serif;

  /* Colors */
  --white: #ffffff;
  --black: #222222;
  --blue: #253cd9;
