.card {
  width: 32%;
  box-shadow: 0 0.25rem 0.9rem 0.25rem rgba(0 0 0 /10%);
  position: relative;
  border-radius: 0.5rem !important;
}

.card__image__content {
  overflow: hidden;
  border-radius: inherit;
}
.card__image {
  width: 100%;
  height: 100%;
  transition: transform 0.5s ease;
}
.card:hover .card__image {
  transform: scale(1.1);
}

.card__content {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0 0 0 /70%);
  color: var(--white);
  padding: 0 0 1.5rem 1rem;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  flex-direction: column;
  border-radius: inherit;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out;
}
.card:hover .card__content {
  opacity: 1;
  visibility: visible;
}

.card__tecnologys {
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}
.card__tecnologys p {
  color: var(--black) !important;
  background-color: #efefef !important;
  opacity: 1;
  padding: 0.1rem 0.7rem;
  border-radius: 0.25rem;
  font-size: 0.9rem;
}

.card__tecnology__links {
  position: absolute;
  top: 5%;
  right: 5%;
  display: flex;
  grid-gap: 0.8rem;
  gap: 0.8rem;
}
.card__icons {
  display: inline-block;
  font-size: 1.5rem;
  color: var(--white);
}
.card__icons:hover {
  color: var(--white);
}

